import { auth } from '../firebase-config.js'; // Asegrate de ajustar la ruta segn la ubicacin de tu archivo
import { signInWithEmailAndPassword, onAuthStateChanged, signOut  } from "firebase/auth";
import { getFirestore, collection, query, where, getDocs, doc, getDoc, addDoc, setDoc, Timestamp, limit, orderBy } from "firebase/firestore";
