// Exponer auth y onAuthStateChanged globalmente si es necesario
window.myAppAuth = auth;
window.myAppOnAuthStateChanged = onAuthStateChanged;

// Funcin para iniciar sesin
function iniciarSesion(email, licencia, rememberme) {
    signInWithEmailAndPassword(window.myAppAuth, email, licencia)
        .then((userCredential) => {
            // Inicio de sesin exitoso
            const user = userCredential.user;
			localStorage.setItem('rememberme', rememberme ? 'true' : 'false');
			localStorage.setItem("emailAdmin", email);
            localStorage.setItem("uidAdmin", user.uid);
            window.location.href = 'paneladmin.html';
        })
        .catch((error) => {
            document.getElementById("mensaje-error").textContent = "No tienes permisos para acceder";
            document.getElementById("mensaje-error").style.display = 'block';
        });
}

// Agregar el manejador de evento submit directamente al formulario e intentar que Google ofrezca recordar contrasea
document.addEventListener('DOMContentLoaded', () => {
    const formularioLogin = document.getElementById('formulario-login-admin');
    if (formularioLogin) {
        formularioLogin.addEventListener('submit', function(event) {
            event.preventDefault();
            const email = document.getElementById("email-admin").value;
            const licencia = document.getElementById("licencia-admin").value;
            const rememberme = document.getElementById("rememberme").checked;
            iniciarSesion(email, licencia, rememberme);
        });
    }

    const botonCerrarSesion = document.getElementById('cerrar-sesion-admin');
    if (botonCerrarSesion) {
        botonCerrarSesion.addEventListener('click', cerrarSesion);
    }
});

// Funcin para cerrar sesin
function cerrarSesion() {
    signOut(auth).then(() => {
        // Sign-out successful.
        console.log("Sesin cerrada con xito");
        // Puedes eliminar aqu otras informaciones almacenadas en localStorage si es necesario
        window.location.href = 'index.html'; // Redirigir a la pgina de inicio
    }).catch((error) => {
        // An error happened.
        console.error("Error al cerrar sesin:", error);
    });
}

// Verificar el estado de autenticacin al cargar la pgina y redirigir si es necesario
window.myAppOnAuthStateChanged(window.myAppAuth, user => {
    const path = window.location.pathname;
    const enPaginaDeInicioDeSesion = path.includes('index.html') || path.endsWith('/kyadmin/') || path.endsWith('/kyadmin');

    if (!user && !enPaginaDeInicioDeSesion) {
        window.location.href = 'index.html';
    } else if (user) {
        console.log("Usuario logueado:", user.email);
        // Si ests en la pgina de inicio de sesin y el usuario est logueado, podras redirigirlo al paneladmin.html
        if (enPaginaDeInicioDeSesion) {
            window.location.href = 'paneladmin.html';
        }
    }
});

// SCRIPTS PARA GENERAR LICENCIAS
async function generarYAgregarLicencias(continuarDesdeUltima = false) {
    const prefijoLicencia = document.getElementById("textolicencia").value;
	const prefijolicenciasgeneradaalumnos = document.getElementById("prefijolicenciasgeneradaalumnos");
    const numeroLicencias = parseInt(document.getElementById("numerolicencias").value, 10);
    const centroEducativo = document.getElementById("selectCentroEducativo").value;
    const idCentro = parseInt(document.getElementById("selectIdCentro").value, 10);
    const idClase = document.getElementById("selectNuevoIdClase").value;
    const ciudad = document.getElementById("selectCiudad").value;
    const anoExpiracion = document.getElementById("anofechaexpiracion").value;
	// Construyo el nuevo prefijo
	const kodigoLicencia = anoExpiracion + prefijoLicencia + idClase;
	console.log("Este es el nuvo prefijo: ", kodigoLicencia);
	
    let nombresLicenciasCreadas = [];
    let ultimoNumeroLicenciaExistente = await obtenerUltimaLicencia(kodigoLicencia);
	let inicioLicencia = 1;
	
	// Validacin de otros campos requeridos
    if (!numeroLicencias || isNaN(numeroLicencias) || !centroEducativo || !ciudad || isNaN(idCentro) || !idClase || !anoExpiracion) {
        mostrarModalRequisitos("Error", "Por favor, asegrate de rellenar todos los campos requeridos correctamente.");
        return;
    }

    // Validacin del prefijo de la licencia
    if (!prefijoLicencia || prefijoLicencia.length < 4) {
        mostrarModalRequisitos("Error", "La licencia debe tener al menos 4 caracteres.");
        return;
    }

	const ultimaLicencia = await obtenerUltimaLicencia(kodigoLicencia);
	if (ultimaLicencia > 0 && !continuarDesdeUltima) {
		try {
			// Espera aqu hasta que el usuario responda
			await mostrarModalConfirmacion(`Una o ms licencias ya existen. El ltimo nmero de licencia existente es ${ultimaLicencia}. Quieres crear las licencias a partir de esta licencia?`);
			// Si el usuario elige "Crear", el flujo contina aqu
			continuarDesdeUltima = true;
		} catch (error) {
			// Si el usuario elige "Cancelar", la ejecucin se detiene aqu
			console.log(error.message); // Manejar el rechazo, posiblemente mostrando un mensaje o log
			return; // Importante: Detiene la ejecucin de la funcin si el usuario cancela
		}
	}
	
	if (continuarDesdeUltima) {
        inicioLicencia = ultimaLicencia + 1;
    }

    // Generacin y agregacin de licencias
    for (let i = ultimoNumeroLicenciaExistente + 1; i <= ultimoNumeroLicenciaExistente + numeroLicencias; i++) {
        const nombreLicencia = `${kodigoLicencia}${i}`;
        const licenciaRef = doc(db, 'licencias', nombreLicencia);
        let fechaExpiracionDate = new Date(`${anoExpiracion}-09-30T12:00:00Z`);
        fechaExpiracionDate.setHours(fechaExpiracionDate.getHours() - 1);
        const fechaExpiracionTimestamp = Timestamp.fromDate(fechaExpiracionDate);

        try {
            await setDoc(licenciaRef, {
                centro: centroEducativo,
                email: '', // Este campo queda vaco
                idCentro: idCentro,
                idClase: idClase,
                nombreciudad: ciudad,
                fechaexpiracion: fechaExpiracionTimestamp
            });
            nombresLicenciasCreadas.push(nombreLicencia);
        } catch (error) {
            console.error(`Error al agregar la licencia ${nombreLicencia}: `, error);
            break;
        }
    }

    mostrarLicenciasModal("Licencias creadas correctamente:", numeroLicencias, nombresLicenciasCreadas);
	document.getElementById('botonCerrarModalLicencias').addEventListener('click', function() {
		console.log("hemos captado el cierre del modal");
		// Llama a la funcin de generar PDF despus de que el modal se cierra
		generarPDFlicencias(centroEducativo, numeroLicencias, idClase, ciudad, anoExpiracion, nombresLicenciasCreadas);
	});

}

