async function generarYAgregarDocente() {
    const nombredocente = document.getElementById("nombredocente").value;
    const emaildocente = document.getElementById("emaildocente").value;
    const centroEducativodocente = document.getElementById("selectCentroEducativoDocente").value;
    const idCentrodocente = parseInt(document.getElementById("selectIdCentroDocente").value, 10);
    const idClasedocente = document.getElementById("selectNuevoIdClaseDocente").value;
    const ciudaddocente = document.getElementById("selectCiudadDocente").value;
    const anoExpiraciondocente = document.getElementById("anofechaexpiracionDocente").value;
	const licenciaprefijodocente = document.getElementById("licenciaprefijodocente").value;
	const licenciageneradadocente = document.getElementById("licenciageneradadocente");
	// Construyo el nuevo nombre de licencia generado automticamente
	const kodigoLicenciadocente = "Profe" + idClasedocente + licenciaprefijodocente + anoExpiraciondocente;
	console.log("Este es el nuvo prefijo: ", kodigoLicenciadocente);
	
	// Validacin de otros campos requeridos
    if (!nombredocente || !emaildocente || !centroEducativodocente || isNaN(idCentrodocente) || !idClasedocente || !ciudaddocente || !anoExpiraciondocente ||!licenciaprefijodocente) {
        mostrarModalRequisitosDocente("Error", "Por favor, asegrate de rellenar todos los campos requeridos correctamente.");
        return;
    }
	
    // Validacin del prefijo de la licencia
    if (!licenciaprefijodocente || licenciaprefijodocente.length < 4) {
        mostrarModalRequisitosDocente("Error", "El licencia prefijo debe tener al menos 4 caracteres.");
        return;
    }

	const existeLicenciaDocente = await comprobarExistenciaLicenciaDocente(kodigoLicenciadocente);
	if (existeLicenciaDocente) {
		try {
			// Espera aqu hasta que el usuario responda
			await mostrarModalExistenciaDocente(`La licencias ${kodigoLicenciadocente} ya est en uso.`);
			return;
		} catch (error) {
			console.log(error.message); // Manejar el rechazo, posiblemente mostrando un mensaje o log
			return; // Importante: Detiene la ejecucin de la funcin si el usuario cancela
		}
	}

    // Generacin y agregacin de docente
    const licenciaProfeRef = doc(db, 'profesores', kodigoLicenciadocente);
    let fechaExpiracionDateDocente = new Date(`${anoExpiraciondocente}-09-30T12:00:00Z`);
    fechaExpiracionDateDocente.setHours(fechaExpiracionDateDocente.getHours() - 1);
    const fechaExpiracionTimestampDocente = Timestamp.fromDate(fechaExpiracionDateDocente);

    try {
        await setDoc(licenciaProfeRef, {
			nombre: nombredocente,
			email: emaildocente,
            centro: centroEducativodocente,
            idCentro: idCentrodocente,
            idClase: idClasedocente,
            licencia: kodigoLicenciadocente,
            fechaexpiracion: fechaExpiracionTimestampDocente
        });
    } catch (error) {
        console.error(`Error al agregar la licencia ${kodigoLicenciadocente}: `, error);
    }

    mostrarLicenciaModalDocente("Licencia creada correctamente:", kodigoLicenciadocente);
	// Si pasa todas las validaciones, procede a generar el PDF
    //generarPDFdocente(nombredocente, emaildocente, centroEducativodocente, idCentrodocente, idClasedocente, ciudaddocente, anoExpiraciondocente, kodigoLicenciadocente);
	document.getElementById('botonCerrarModalLicenciaDocente').addEventListener('click', function() {
		// Llama a la funcin de generar PDF despus de que el modal se cierra
		generarPDFdocente(nombredocente, emaildocente, centroEducativodocente, idCentrodocente, idClasedocente, ciudaddocente, anoExpiraciondocente, kodigoLicenciadocente);
	});
}

