// COMPROBAR LICENCIA EXISTENTE DOCENTE
async function comprobarExistenciaLicenciaDocente(kodigoLicenciadocente) {
    const licenciasRef = collection(db, "profesores");
    const q = query(licenciasRef, where("licencia", "==", kodigoLicenciadocente));
    const querySnapshot = await getDocs(q);

    if (querySnapshot.empty) {
        console.log("No existe un documento con el cdigo de licencia proporcionado.");
        return false; // Retorna false para indicar que no se encontr el documento
    } else {
        console.log("Existe un documento con el cdigo de licencia proporcionado.");
        querySnapshot.forEach(doc => {
            // Aqu puedes manejar los datos del documento encontrado
            console.log(doc.id, " => ", doc.data());
        });
        return true; // Retorna true para indicar que se encontr al menos un documento
    }
}

