// Funcin para comprobar el siguiente idCentro disponible
async function comprobarIdCentro() {
    const btnComprobar = document.getElementById('comprobarIdCentro');
    btnComprobar.addEventListener('click', async () => {
        const idCentros = await obtenerTodosLosIdCentro();
        const primerIdDisponible = encontrarPrimerIdCentroDisponible(idCentros);
    });
    btnComprobar.classList.add("listener-added");
}

// Funcin para obtener todos los idCentro de Firestore
const obtenerTodosLosIdCentro = async () => {
  const idCentros = [];
  const snapshot = await getDocs(collection(db, 'centros'));
  snapshot.forEach(doc => {
    if (doc.data().idCentro && typeof doc.data().idCentro === 'number') {
      idCentros.push(doc.data().idCentro);
    }
  });
  return idCentros;
};


// Funcin para encontrar el primer idCentro disponible
const encontrarPrimerIdCentroDisponible = (idCentros) => {
    // Asegurarse de que los ID de centro estn ordenados numricamente
    idCentros.sort((a, b) => a - b);
    
    // Iniciar la bsqueda del primer hueco desde 1
    let primerIdDisponible = 1;

    for (let i = 0; i < idCentros.length; i++) {
        // Si el ID actual es igual al que estamos buscando, incrementamos para buscar el siguiente
        if (idCentros[i] === primerIdDisponible) {
            primerIdDisponible++;
        } else {
            // Si encontramos un ID mayor sin haber encontrado un hueco, el loop se detiene
            // y retornamos el primerIdDisponible, que ser el primer hueco disponible
            break;
        }
    }
	
    return primerIdDisponible;
	
};

// AGREGAR CENTRO - Firestore
// Funcin para agregar un nuevo centro con el nombre en el documento generado
async function agregarNuevoCentro() {
    const nombreCentro = document.getElementById('nombrenuevocentro').value.trim();
    const ciudad = document.getElementById('ciudadnuevocentro').value.trim();
    const idCentro = parseInt(document.getElementById('idnuevocentro').value, 10);

    if (!nombreCentro || !ciudad || isNaN(idCentro)) {
        mostrarModal("Por favor, rellena todos los campos correctamente.");
        return;
    }
	
	 // Comprobar si el idCentro ya existe
    const centrosConId = await getDocs(query(collection(db, 'centros'), where('idCentro', '==', idCentro)));
    if (!centrosConId.empty) {
        // Si ya existe un centro con el mismo idCentro, mostrar un modal y detener la ejecucin
        mostrarModal(`El idCentro ${idCentro} ya est en uso. Por favor, elige otro.`);
        return;
    }

    try {
        // Utiliza setDoc en vez de addDoc para especificar el ID del documento
        // Reemplaza espacios en nombreCentro por guiones o cualquier otro caracter para evitar errores
        const docId = nombreCentro.replace(/\s+/g, '-').toLowerCase();
        await setDoc(doc(db, 'centros', docId), {
            centroeducativo: nombreCentro,
            idCentro: idCentro,
            nombreciudad: ciudad
        });
        mostrarModal("Centro educativo agregado correctamente:", nombreCentro, idCentro);
    } catch (error) {
        console.error("Error al aadir el documento: ", error);
        mostrarModal("Error al aadir el centro educativo:", nombreCentro, idCentro);
    }
}


