// Funcin modal centro agregado evitando vulneracin Cross-Site Scripting
function mostrarModal(mensaje, nombreCentro = '', idCentro = null) {
    const modal = document.getElementById("miModal");
    const texto = document.getElementById("mensajeModal");
	const span = document.getElementsByClassName("close")[0];

    // Limpiar el contenido previo
    texto.innerHTML = '';

    // Agrega el mensaje principal como texto plano
    const mensajeNode = document.createTextNode(mensaje);
    texto.appendChild(mensajeNode);

    // Agrega el nombre del centro en negrita si se proporciona
    if (nombreCentro) {
        const strongNombre = document.createElement('strong');
        strongNombre.textContent = nombreCentro;
        texto.appendChild(document.createTextNode(' ')); // Espacio antes del nombre del centro
        texto.appendChild(strongNombre);
    }

    // Agrega el idCentro en negrita si se proporciona
    if (idCentro !== null) {
        const strongId = document.createElement('strong');
        strongId.textContent = ` con ID ${idCentro}.`;
        texto.appendChild(document.createTextNode(' ')); // Espacio antes del ID
        texto.appendChild(strongId);
    }

    // Mostrar el modal
    modal.style.display = "block";
	// Ocultar el modal
	span.onclick = function() {
        modal.style.display = "none";
    };

    // Cerrar el modal si el usuario hace clic fuera de l
    window.onclick = function(event) {
        if (event.target == modal) {
            modal.style.display = "none";
        }
    };
}

