// PDF LICENCIAS
function generarPDFlicencias(centroEducativo, numeroLicencias, idClase, ciudad, anoExpiracion, nombresLicenciasCreadas) {
	console.log("Estamos en la funcin generar pdf");
	// Objeto para mapear idClase a Clasedocente
	const clases = {
		"4a": "4 ESO | Grupo A",
		"4b": "4 ESO | Grupo B",
		"4c": "4 ESO | Grupo C",
		"4d": "4 ESO | Grupo D",
		"4e": "4 ESO | Grupo E",
		"1BachA": "1 Bachillerato | Grupo A",
		"1BachB": "1 Bachillerato | Grupo B",
		"1BachC": "1 Bachillerato | Grupo C",
		"1BachD": "1 Bachillerato | Grupo D",
		"1BachE": "1 Bachillerato | Grupo E",
		"2BachA": "2 Bachillerato | Grupo A",
		"2BachB": "2 Bachillerato | Grupo B",
		"2BachC": "2 Bachillerato | Grupo C",
		"2BachD": "2 Bachillerato | Grupo D",
		"2BachE": "2 Bachillerato | Grupo E"
	  // Agrega ms mapeos segn sea necesario
	};
	
	const Clasedocente = clases[idClase];
	
	if (window.jspdf && window.jspdf.jsPDF) {
		const { jsPDF } = window.jspdf;
		const doc = new jsPDF();
		// Obtener la fecha actual
		const fecha = new Date();
		const fechaActual = fecha.getDate() + "/" + (fecha.getMonth() + 1) + "/" + fecha.getFullYear();
		const xInicial = 15; // La posicin inicial X para imprimir las licencias
		let x = xInicial; // La posicin X actual que se ir actualizando
		const maxPorLinea = 4; // Mximo de nombres de licencia por lnea
		const stepX = 45; // El paso horizontal despus de cada nombre de licencia
		let y = 230; // Inicia la impresin en la posicin Y = 20
		const stepY = 10; // El paso vertical despus de cada lnea de licencia
				
		// Aadir una imagen 'imagenBase64' con cadena base64
        const imagenChica64 = '';
		doc.addImage(imagenChica64, 'JPEG', 130, 90, 50, 50);
		
		const imagenLogo64 = '';
		doc.addImage(imagenLogo64, 'PNG', 0, 0, 70, 55);

		// Aadir un encabezado
        doc.setFontSize(18);
        doc.text('Detalle de Licencias Alumnos', 65, 30); // Cambia las coordenadas segn sea necesario
		doc.setFontSize(12);
		doc.text(`Combina juego y enseanza | Crea entorno educativo!`, 55, 40);
		// Aadir texto al documento
		doc.setFontSize(12)
		doc.text(`${fechaActual}`, 170, 15);
		doc.text(`Hola ${centroEducativo}! bienvenido al SaaS Neodogma donde los alumnos aprendrn`, 15, 60);
		doc.text(`las bases de la actual Biologa Molecular, con trminos de Gentica y Biotecnologa.`, 15, 70);		
		doc.text(`Estos son los datos que hemos registrado:`, 15, 90);
		doc.text(`* Nmero de licencias: ${numeroLicencias}`, 15, 100);
		doc.text(`* Centro Educativo: ${centroEducativo}`, 15, 110);
		doc.text(`* Clase: ${Clasedocente}`, 15, 120);
		doc.text(`* Ciudad: ${ciudad}`, 15, 130);
		doc.text(`* Expiracin licencias: 30 Sept ${anoExpiracion}`, 15, 140);
		doc.text(`Ahora podis descargar nuestra app y registrar cada licencia con cada alumno`, 15, 160);
		doc.text(`* Recuerda, que cada licencia solo puede ser registrada en un nico dispositivo.`, 15, 170);
		doc.text(`Podis descargar nuestra app Dogma en las siguientes plataformas:`, 15, 180);
		doc.text(`* Google Play: https://play.google.com/store/apps/details?id=com.Youmu.Dogma.android&hl=es_419`, 15, 190);
		doc.text(`* App Store: https://apps.apple.com/es/app/dogma/id6476204721`, 15, 200);
		doc.text(`Licencias generadas:`, 15, 220);
		
		nombresLicenciasCreadas.forEach((nombreLicencia, index) => {
			// Imprime el nombre de la licencia en la posicin actual
			doc.text(nombreLicencia, x, y);

			// Verifica si es momento de saltar a la prxima lnea o continuar en la misma
			if ((index + 1) % maxPorLinea === 0) {
				y += stepY; // Avanza hacia abajo para empezar una nueva lnea
				x = xInicial; // Restablece X a la posicin inicial para la nueva lnea
			} else {
				x += stepX; // Avanza a la derecha para el siguiente nombre en la misma lnea
			}

			// Si 'y' es mayor que el lmite de la pgina, agrega una nueva pgina y resetea 'y' y 'x'
			if (y > 280) {
				doc.addPage();
				y = 20; // Resetea Y al inicio para la nueva pgina
				x = xInicial; // Restablece X a la posicin inicial para la nueva pgina
			}
		});

		// Previsualizr el PDF
		const blob = doc.output("blob");
		previsualizarPDFlicencias(blob);
		
		// Guardar el PDF
		//doc.save('Licencia-Docente.pdf');
	} else {
		console.error('jsPDF no est disponible.');
	}
}

