// Previsualizar el PDF DOCENTE en una pgina web
function previsualizarPDFdocente(blob) {
    // Crea una URL para el blob
    const url = URL.createObjectURL(blob);

    // Opcin 1: Abrir el PDF en una nueva pestaa del navegador
    window.open(url);
}

